body {
  margin: 0;
  padding: 0;
  color: #0E0E0E;
  min-width: 320px;
  background-color: #101c2d; }

html {
  box-sizing: border-box;
  overflow-y: scroll;
  /* margin-left: calc(100vw - 100%);
  margin-right: 0; */ }

*,
*::before,
*::after {
  box-sizing: inherit; }

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0; }

p {
  margin: 0;
  padding: 0; }

ul {
  padding: 0; }

.container {
  max-width: 1255px;
  margin: 0 auto;
  padding: 0 15px 0 15px; }

.header {
  width: 100%;
  height: 115px;
  background-color: rgba(16, 28, 45, 0.5);
  position: fixed;
  transition: 0.5s; }
  .header:hover {
    background-color: #101c2d;
    box-shadow: 0px 10px 29px -3px #000000; }

.nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 17px 0 17px 0; }

.nav ul {
  display: flex;
  align-items: center; }

.nav li {
  list-style: none;
  margin: 0 0 0 30px; }

.nav a {
  text-decoration: none;
  font-size: 16px;
  line-height: 19px;
  color: #7BB5D2; }

.wrapper {
  display: flex; }

.burger {
  display: none;
  color: #fff; }

@media (max-width: 976px) {
  .nav ul {
    display: block;
    text-align: center; }
  .wrapper {
    position: absolute;
    display: block;
    background-color: #222220;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: 1;
    position: fixed; }
  .burger {
    display: block; } }

.content {
  display: flex;
  justify-content: space-between; }

.content__cards {
  width: 69%; }

.content__stat {
  width: 29%; }

.content__cards-item {
  width: 100%;
  height: 445px;
  background-color: aqua;
  margin: 0 0 20px 0; }

.content__stat-item {
  width: 100%;
  height: 112px;
  background-color: aqua;
  margin: 0 0 20px 0; }

.section-slider {
  width: 100%;
  height: 577px;
  background-image: url(../img/bg.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  padding-top: 115px; }

.burger {
  width: 14px;
  height: 3px;
  background-color: #fff;
  position: relative;
  top: -10px; }

.burger::before {
  position: absolute;
  content: '';
  top: 10px;
  left: -8px;
  z-index: 1;
  width: 22px;
  height: 3px;
  background-color: #fff; }

.burger::after {
  position: absolute;
  content: '';
  top: -10px;
  left: -8px;
  z-index: 1;
  width: 22px;
  height: 3px;
  background-color: #fff; }
